import Vue from 'vue'
import vueRouter from 'vue-router'

Vue.use(vueRouter)

const routes = [
    {
        path: '/login',
        name: 'login',
        component: () => import('../views/Login.vue'),
        meta: {isPublic:true}
    },
    {
        path: '/',
        name: 'main',
        component: () => import('../views/Main.vue'),
        children:[
            {
                path: 'categories/create',
                name: 'categoryEdit',
                component: () => import('../views/CategoryEdit.vue'),
                
            },
            {
                path: 'categories/edit/:_id',
                name: 'categoryEdit',
                component: () => import('../views/CategoryEdit.vue'),
                props: true
            },
            {
                path: 'categories/list',
                name: 'categoryList',
                component: () => import('../views/CategoryList.vue')
            },

            {
                path: 'items/create',
                name: 'itemsEdit',
                component: () => import('../views/ItemsEdit.vue'),
            },
            {
                path: 'items/edit/:_id',
                name: 'itemsEdit',
                component: () => import('../views/ItemsEdit.vue'),
                props: true
            },
            {
                path: 'items/list',
                name: 'itemsEdit',
                component: () => import('../views/ItemsList.vue')
            },

            {
                path: 'heros/create',
                name: 'herosEdit',
                component: () => import('../views/HerosEdit.vue'),
            },
            {
                path: 'heros/edit/:_id',
                name: 'herosEdit',
                component: () => import('../views/HerosEdit.vue'),
                props: true
            },
            {
                path: 'heros/list',
                name: 'herosList',
                component: () => import('../views/HerosList.vue')
            },


            {
                path: 'articles/edit/:_id',
                name: 'articlesEdit',
                component: () => import('../views/ArticleEdit.vue'),
                props: true
            },
            {
                path: 'articles/create',
                name: 'articlesEdit',
                component: () => import('../views/ArticleEdit.vue')
            },
            {
                path: 'articles/list',
                name: 'articlesList',
                component: () => import('../views/ArticleList.vue')
            },

            {
                path: 'ads/edit/:_id',
                name: 'adsEdit',
                component: () => import('../views/AdEdit.vue'),
                props: true
            },
            {
                path: 'ads/create',
                name: 'adsEdit',
                component: () => import('../views/AdEdit.vue')
            },
            {
                path: 'ads/list',
                name: 'adsList',
                component: () => import('../views/AdList.vue')
            },

            {
                path: 'admin_user/edit/:_id',
                name: 'adminUserEdit',
                component: () => import('../views/AdminUserEdit.vue'),
                props: true
            },
            {
                path: 'admin_user/create',
                name: 'adminUserEdit',
                component: () => import('../views/AdminUserEdit.vue')
            },
            {
                path: 'admin_user/list',
                name: 'adminUserList',
                component: () => import('../views/AdminUserList.vue')
            }
        ]
    }
]

const router = new vueRouter({
    mode: 'history',
    routes
})

router.beforeEach((to,from,next)=>{
    // if(!localStorage.token){  //只这样写的话 会报栈溢出的错误，因为一开始登录login也没有token，一直循环
    //     next('/login');
    // }

    if(!to.meta.isPublic&&!localStorage.token){ //给'/login'路径增加一个特殊的路由元
        // console.log(111)
        next('/login');
    }
    next();
})
export default router;