<template>
  <div id="app">
    <router-view v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
  provide() {
    return{
      //provide＆inject一起搭配使用
      //provide是为自己所有的后代注册一个依赖
      //后代通过reject['reload']引入这个依赖然后使用
      reload: this.reload 
    }
  },
  data(){
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload(){
      this.isRouterAlive = false;
      this.$nextTick(()=>{
        this.isRouterAlive = true;
      })
    }
  },
}
</script>

<style>
html,body{
  margin: 0;
  padding: 0;
}
</style>
